.example {
  padding: 25px 20px;

  background-color: $color-gray-100;
}

.example__heading {
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  line-height: 1.1;
  font-style: normal;
  font-weight: 400;
  color: $color--black;

  margin: 0;
}

.example__description {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.3;
  font-style: normal;
  font-weight: 400;
  color: $color-gray-700;

  margin: 0;
}

.example__item {
  margin: 0;
}

.example__title {
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  line-height: 1;
  font-style: normal;
  font-weight: 400;

  color: $color--black;
}

.example__definition {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 1;
  font-style: normal;
  font-weight: 400;

  margin-left: 0;

  color: $color-gray-700;
}

.example__cost {
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 1.4;
  font-style: normal;
  font-weight: 400;
  text-transform: uppercase;

  color: $color--black;
}
